import { AgentMessageState, GroundingMessage } from "../useAgent.types"
import { mergeCoordinatesData } from "../utils/mergeCoordinatesData"

function handleGroundingMessage(
  agentState: AgentMessageState,
  data: GroundingMessage,
): AgentMessageState {
  return {
    ...agentState,
    references: {
      ...agentState.references,
      [data.messageId]: mergeCoordinatesData(agentState.references[data.messageId], data),
    },
  }
}

export { handleGroundingMessage }
